import { Checks } from './checks';
import { ErrorObject } from '../../classes/errorObject';

export class LoginDTO{

    email: string;
    password: string;
    isEncrypted: boolean;
    errors: ErrorObject[] = [];

    constructor(Obj: any){

        this.email = Obj.email;
        this.password = Obj.password;
        this.isEncrypted = Obj.isEncrypted;

    }

    check(){

        if(!this.email){
            this.errors.push(new ErrorObject("email", "falta el email"));
        }else if(!Checks.checkEmail(this.email)){
            this.errors.push(new ErrorObject("email", "email no valid"));
        }

        if(!this.password){
            this.errors.push(new ErrorObject("password", "falta la password"));
        }else if(this.isEncrypted?!Checks.EncryptPassword(this.password):!Checks.checkPassword(this.password)){
            this.errors.push(new ErrorObject("password", "password no valida"));
        }

        return this.errors;
    }

    prepareToSend(){

            return {
                email: this.email,
                password: this.isEncrypted?this.password:Checks.EncryptPassword(this.password)
             }

    }

    static encryptPassword(password: string){
        return Checks.EncryptPassword(password);
    }


}