import { Router, ActivatedRoute } from '@angular/router';
import { MainService } from '../../../services/main.service';
import { Utils } from 'src/app/in2art/services/utils.service';
import { Component, HostListener, OnInit } from '@angular/core';

@Component({
    selector: 'app-verify-account',
    templateUrl: './verify-account.component.html',
    styleUrls: ['./verify-account.component.scss']
})
export class VerifyAccountComponent implements OnInit {

    public activeButton: boolean = true;
    public emailCookies: string;
    timeLeft: number = 30;
    interval;


    constructor(
        public main: MainService,
        public router: Router,
    ){

    }

    async ngOnInit() {
        this.emailCookies = Utils.getCookie('email');

        if(Utils.getCookie('refreshParams')){
            let refreshParams = JSON.parse(Utils.getCookie('refreshParams'));
            this.activeButton = refreshParams.activeButton;
            this.timeLeft = refreshParams.timeLeft;
            if(!this.activeButton){
                this.countDownTimer();
            }
        }
    }

    ngOnDestroy() { this.main.onDestroyPage(); }

    countDownTimer(){
        this.interval = setInterval(() => {
            if(this.timeLeft > 0) {
                this.timeLeft--;
            } else {
                this.timeLeft = 30;
                this.pauseTimer();
                this.activeButton = true;
            }
        },1000)
    }

    pauseTimer() {
        clearInterval(this.interval);
    }

    @HostListener("window:beforeunload", ["$event"]) unloadHandler(event: Event) {
        this.onRefreshData();
    }

    onRefreshData(){
        let refreshParams = {
            activeButton : this.activeButton,
            timeLeft: this.timeLeft,
        };
        Utils.setCookie('refreshParams', JSON.stringify(refreshParams), 1);
    }

    reSendEmail(){
        if(this.activeButton){
            this.main.provider.reenviarCorreuActivacio(this.emailCookies).then(
                (data: any) => {
                    if(data == 409){ //En el cas de que salti el codgi de 409 vol dir que entra en conflicta amb la base de dades i aixo vol dir que el correu ja esta verificat
                        this.main.toastDanger("Aquesta compte ja està activada!");
                        Utils.removeCookie('email');
                        this.main.goTo('/auth/login'); //Redirigim a la pagina de login
                    }
                }
            );
            this.activeButton = false;
        }
    }

}
