import { MainService } from './../../../services/main.service';

import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Checks } from 'src/app/in2art/services/DTOs/checks';

@Component({
    selector: 'app-reset-password',
    templateUrl: './reset-password.component.html',
    styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

    
    public email: any;
    public emailForm;
    public form: FormGroup;
    public carregant = false;

    public res;


    constructor(public main: MainService,
        private fb: FormBuilder,) {
        main.newPage(this);

        this.form = this.fb.group({
            email: ["", [Validators.required, Validators.email]],
        })
    }

    async ngOnInit() {
    }
    ngOnDestroy() { this.main.onDestroyPage(); }

    async enviarEmail() {
        this.carregant = true;
        if(Checks.checkEmail(this.form.controls.email.value)){
            
            this.main.provider.sendEmailforResetPassword(this.form.controls.email.value).then((res: any) => {
                if(res == 404){
                    let msgErr = this.form.controls.email.value + " doesn't exist";
                    this.main.toastDanger(msgErr);
                }else{
                    let msgSucces = "Email sent to " + this.form.controls.email.value;
                    this.main.toastSuccess(msgSucces);
                    this.carregant = false;
                }
            });
        }
        // this.res = await this.main.provider.recuperarPasswordEnviarEmail(this.email);
        this.carregant = false;

        // if (!this.res) {
        //     this.main.toastDanger(this.main.t("Error sending email to") + " " + this.email);
        //     this.emailForm = this.email;
        //     this.email = "";


    }




}
