import { Checks } from './checks';
import { ErrorObject } from '../../classes/errorObject';

export class RegisterDTO{

    name: string;
    lastName: string;
    email: string;
    nickname: string;
    password: string;
    profileTypeId: string;
    photo = {
        dataBase64: "",
        fileName: "",
        fileExt: ""
    };
    birthDate: string;
    address: string;
    phone: string;


    constructor(Obj: any) {
        this.name = Obj.name;
        this.lastName = Obj.lastName;
        this.email = Obj.email;
        this.nickname = Obj.nickname;
        this.password = Obj.password;
        this.profileTypeId = Obj.profileTypeId;
        this.photo = {
            dataBase64: Obj.photo.dataBase64,
            fileName: Obj.photo.fileName,
            fileExt: Obj.photo.fileExt
        };
        this.birthDate = Obj.birthDate;
        this.address = Obj.address;
        this.phone = Obj.phone;
    }   

    /**
     * Funcio en la que comprovem que tots els camps estiguin omplerts i siguin valids per a poder completar el registre
     * @returns Array de string amb els errors trobats
     */
    check(){

        const errors: ErrorObject[] = [];

        //miro que estiguin tots els camps necessaris
        if (!this.name){
            errors.push(new ErrorObject("name", 'Falta el nom'));
        }

        if(this.name){
            this.name = this.name.trim();
            if(this.name.length == 0 || this.name.length > RegisterDTO.MaxNameLength){
                errors.push(new ErrorObject("name", 'El nom no te una longitud valida'));
            }
        }

        if(!this.nickname){
            errors.push(new ErrorObject("nickname", "falta el username"));
        }

        if(this.nickname){
            this.nickname = this.nickname.trim();
            if(this.nickname.length == 0 || this.nickname.length > RegisterDTO.MaxNicknameLength){
                errors.push(new ErrorObject("nickname", 'El nickname no te una longitud valida'));
            }
        }

        if (this.lastName) {
            this.lastName = this.lastName.trim();
            if (this.lastName.length == 0 || this.lastName.length > RegisterDTO.MaxLastNameLength) {
                errors.push(new ErrorObject( "lastName", "el cognom no te una longitud valida"));
            }
        }

        if (!this.email) {
            errors.push(new ErrorObject("email", "falta el email"));
        } else if (!Checks.checkEmail(this.email)) {
            errors.push(new ErrorObject("email", "el email no es valid"));
        }
        if (!this.password) {
            errors.push(new ErrorObject("password", "falta la contrasenya"));
        }
        if (this.password) {
            if (!Checks.checkPassword(this.password)) {
                errors.push(new ErrorObject("password","la contrasenya no es valida"));
            }
        }
        if (!this.birthDate) {
            errors.push(new ErrorObject( "birthDate", "falta la data de naixement"));
        }
        if (this.birthDate) {
            if (!Checks.checkData(this.birthDate)) {
                errors.push(new ErrorObject( "birthDate", "la data no es valida"));
            }
        }

        if (this.address) {
            if (this.address.length == 0 || this.address.length > RegisterDTO.MaxAddressLength) {
                errors.push(new ErrorObject ("address", "la direccio no te una longitud valida"));
            }
        }

        if (this.phone) {
            if (!Checks.checkPhone(this.phone)) {
                errors.push(new ErrorObject( "phone", "el telefon no es valid"));
            }
        }
        if (!this.profileTypeId) {
            errors.push(new ErrorObject("profileTypeId", "falta el tipus de perfil"));
        }
        if (this.profileTypeId) {
            if (!Checks.checkID(this.profileTypeId)) {
                errors.push(new ErrorObject("profileTypeId","el tipus de perfil no es valid"));
            }
        }
        if (this.photo) {
            //check if is a base64 string format
            if (!Checks.checkBase64(this.photo.dataBase64)) {
                errors.push(new ErrorObject("invalidPhoto", "la foto no es valida"));
            }
            if (!this.photo.fileName || this.photo.fileName.length == 0) {
                errors.push(new ErrorObject("photoFileName", "falta el nom de la foto"));
            }
            if (!this.photo.fileExt || this.photo.fileExt.length == 0) {
                errors.push(new ErrorObject("photoFileExt", "Extensio de la foto no valida"));
            }

        if(!this.photo) {
            errors.push(new ErrorObject("missingPhoto", "falta la foto"));
        }

        }


        return errors;

    }

    static MaxNameLength: number = 150;

    static MaxAddressLength: number = 9999

    static MaxLastNameLength: number = 250;

    static MaxNicknameLength: number = 150;


}



