import { Checks } from './checks';
import { ErrorObject } from '../../classes/errorObject';
import { ProviderService } from '../provider.service';

export class InputCheckDTO{

    constructor(
        public provider: ProviderService
    ){}

    public response: any;
    public exists: any

     async validateEmail(email: string): Promise<boolean> {
        if(Checks.checkEmail(email)){
            await this.provider.checkEmail(email).then((res: any) => {
                this.response = res;
                this.exists = this.response.exist;
            });
        }
        return this.exists;
    }

    async validateNickname(nickname: string): Promise<boolean> {
        if(Checks.checkNickname(nickname)){
            await this.provider.checkNickname(nickname).then((res: any) => {
                this.response = res;
                this.exists = this.response.exist;
            });
        }
        return this.exists;
    }

}